import React from "react";

export const PaznokcieDystroficzne = () => {
  return (
    <div>
      <h1 className="h1">PAZNOKCIE DYSTROFICZNE</h1>
      <div className="li">
        <p>
          Przeronite i zgrubiae paznokcie mog powstawa z przyczyn
          zewntrznych (zbyt ciasne obuwie), wewntrznych (przypadoci
          genetyczne) lub z powodu wieku i chorb takich jak: cukrzyca czy
          uszczyca. Przeronite paznokcie mog powodowa zarwno dyskomfort
          estetyczny, jak i przyczynia si do utrudnienia wykonywania
          codziennych czynnoci jak np. chodzenie czy zakadanie butw.
          Przeronite i zgrubiae paznokcie mog obejmowa wszystkie palce lub
          jeden palec, ktrym zazwyczaj jest paluch.
        </p>
        <br></br>
        <h3>Przyczyny</h3>
        <span>
          <ul>
            <li>uraz mechaniczny</li>
            <li>wielokrotne zerwanie pytki paznokciowej</li>
            <li>infekcje grzybiczne, bakteryjne, wirusowe,</li>
            <li>choroby oglnoustrojowe</li>
            <li>choroby skrne</li>
            <li>chemioterapia</li>
            <li>nieprawidowa biomechanika chodu</li>
            <li>inne czynniki</li>
          </ul>
        </span>
        <br></br>
        <h3>Czym sie charakteryzuj?</h3>
        <span className="numbers-li">
          <ol>
            <li>Zwolnionym lub zahamowanym wzrostem</li>
            <li>Zmian zabarwienia (te, brunatne, brzowe, inne)</li>
            <li>Zmian gruboci, znaczn lub niewielk</li>
            <li>Mog wystpowa dolegliwoci blowe</li>
            <li>Trudno w skracaniu</li>
            <li>Kruchoci i brakiem elastycznoci</li>
          </ol>
        </span>
      </div>
    </div>
  );
};
