import React, { useState } from "react";
import jsonDlonie from "../Json/dlonie.json";

import SelectDetailesOfDlonie from "../dlonie/procedureDetailes/SelectDetailesDlonie";
import "./styleDlonie/dlonie.scss";

import MySwiper from "../../swiper/MySwiper";

function Dlonie() {
  const [idItems, setIdItems] = useState(-1);

  const clickChooseSelect = (data) => {
    setIdItems(data);
  };

  return (
    <div className="dlonie">
      <header className="dlonie--header">
        <p className="dlonie__header--p">
          W swojej pracy cz zamiowanie do podologii z chci pomagania
          innym. Motywacj do pracy jest dla mnie nieustanne niesienie ulgi
          potrzebujcym pacjentom. W gabinecie utrzymuj medyczny standard
          higieny. Stosuj nowoczesne i skuteczne metody terapii.
        </p>
        <p className="dlonie__header--p">
          W dbaoci o Pastwa bezpieczestwo przy kadym zabiegu wykorzystywane
          s tylko wysterylizowane narzdzia oraz zachowywane s wszelkie zasady
          higieny.
        </p>
      </header>
      <MySwiper json={jsonDlonie} clickChooseSelect={clickChooseSelect} />
      <SelectDetailesOfDlonie id={idItems} />
    </div>
  );
}

export default Dlonie;
