import React from "react";

export const Depilacja = () => {
  return (
    <div>
      <h1 className="h1">Depilacja</h1>
      <article className="">
        <h3>Jak wyglda zabieg depilacji woskiem?</h3>
        <p>
          Depilacja woskiem polega na usuwaniu owosienia przy pomocy
          rozgrzanego wosku. Woski usuwa si w sposb mechaniczny, wyrywajc je
          w trakcie zrywania zastygego wosku. Jego zadaniem jest bowiem
          przywarcie do wosw i usunicie ich wraz z cebulkami. Taka metoda
          pozwala o wiele duej cieszy si gadsz skr, poniewa woski
          musz odrasta od podstaw. Uywajc maszynki, nigdy nie uzyska si
          podobnego efektu, z uwagi na fakt, e woski o wiele szybciej odrosn
          - bo s jedynie skracane. Depilacja woskiem polega na naoeniu na
          skr rozgrzanego, pynnego wosku, zgodnie z kierunkiem wzrostu wosa.
          Wosk bardzo dokadnie oblepia woski, wnikajc do gboko, by je
          pniej wyrwa razem z cebulkami. Co istotne, podobnej depilacji mona
          podda dowoln parti ciaa - to znaczy zarwno pachy, jak i nogi,
          okolice bikini, ramiona, jak i obszar nad grn warg, by usun
          pojawiajcy si tam czsto nawet u kobiet wsik.
        </p>
        <h3>Zalety depilacji przy uyciu wosku</h3>
        <p>
          Depilacja przy uyciu wosku przynosi o wiele lepsze efekty - warto
          doda, e dugotrwae. Jeden zabieg moe wystarczy na okoo 2-5
          tygodni. Wszystko zaley od indywidualnych uwarunkowa, jednak zawsze
          to moliwo duszego cieszenia si gadk skr. Jednoczenie na
          zabieg mog przyj wszyscy, ktrzy tylko chc - zarwno modzi, jak i
          starsi, kobiety, jak i mczyni. Coraz wicej panw decyduje si na
          depilacj woskiem z powodu zbyt bujnego owosienia na przykad na
          plecach czy klatce piersiowej.
        </p>
        <p>
          Zalet depilacji jest o wiele wicej. Przede wszystkim regularne jej
          wykonywanie osabia woski i sprawia, e za kadym kolejnym razem
          odrastaj wolniej i s ciesze. Efekt gadkiej skry jest
          natychmiastowy, a do tego prezentuje si ona o wiele lepiej. Dlaczego?
          Poniewa depilacja woskiem dziaa rwnie jak peeling, usuwajc
          wierzchnie, martwe czci naskrka. Pojawia si na powierzchni skra
          gadka, modsza, adniejsza - co jest korzystne dla naszego wygldu.
          Co wicej, u wielu osb po depilacji przy uyciu maszynki pojawia si
          podranienie, pieczenie lub wid skry. Depilacja woskiem nie stwarza
          takich problemw i nie wywouje podobnego dyskomfortu.
        </p>
        <h3>Przeciwwskazania do depilacji woskiem</h3>
        <p>
          Depilacja jest zabiegiem bezpiecznym i polecanym wszystkim - rwnie i
          tym, ktrzy posiadaj skr wraliw.
        </p>
        <p>
          Istnieje kilka przeciwwskaza do stosowania tego zabiegu, o ktrych
          naley zawsze pamita. S to:
        </p>
        <ul>
          <li>rozszerzone naczynia wosowate lub ylaki</li>
          <li>zapalenie y- alergia na wosk</li>
          <li>zapalenie mieszkw wosowych</li>
          <li>stany zapalne i ropne skry</li>
          <li>
            zbyt krtkie wosy (odpowiednia do zabiegu dugo to 5-10
            milimetrw)
          </li>
          <li>skra bezporednio po ekspozycji na soce lub promienie UV.</li>
        </ul>
        <h3>Jak czsto powtarza zabieg depilacji?</h3>
        <p>
          Zabieg depilacji powinien by powtarzany co miesic - jednak wszystko
          zaley od indywidualnych przypadkw. U kadego czowieka wosy rosn z
          nieco inn szybkoci - poza tym osoby z ciemniejszymi wosami zwykle
          maj grubsze wosy i szybciej pojawiaj si u nich na skrze ciemne
          kropki, gdy woski odrastaj.
        </p>
        <p>
          Usuwanie ich przy pomocy depilacji i tak jest jednak wygodniejsze,
          poniewa wszystko to wystpuje o wiele pniej. Z racji na fakt, e u
          kadej osoby wosy rosn z inn prdkoci, dobiera si czstotliwo
          wizyt do kadej klientki czy klienta. Wosy odrastaj w przedziale 3-6
          tygodni, przy czym by byo moliwe ich ponowne wyrwanie, musz
          osign co najmniej 5 milimetrw dugoci.
        </p>
      </article>
    </div>
  );
};
