import React from "react";

export const WrastajacePaznokcie = () => {
  return (
    <section>
      <h1 className="h1">WRASTAJCE PAZNOKCIE</h1>
      <header className="pp">
        <p>
          Wrastajce paznokcie to Zmiana chorobowa dotyczca zarwno dzieci,
          osb modych, jak i dojrzaych, powodujca dyskomfort lub bl o Rnym
          nasileniu w zalenoci od Stadium zaawansowania. Problem najczciej
          pojawia si na najwikszym palcu stopy, ale take na innych pacach
          stopy. niekiedy na innych palcach stopy, poprzez wbijanie lub
          uciskanie boczn krawdzi pytki paznokciowej tkanki wau
          paznokciowego.
        </p>
        <p>
          <br></br> Szybka reakcja to gwarancja cakowicie bezbolesnego i
          nieinwazyjnego leczenia w naszym gabinecie pedologicznym.
        </p>
        <br></br>
      </header>
      <main>
        <h3>Przyczyny</h3>
        <span className="li">
          <ul>
            <li>
              niewaciwe obuwie: wysoki obcas, buty zbyt wskie w palcach
            </li>
            <li>
              nieprawidowe obcinanie paznokci, regularne wycinanie bokw
              pytek
            </li>
            <li> nadmierna sucho lub potliwo skry</li>
            <li> deformacje ortopedyczne</li>
            <li> uwarunkowania genetyczne</li>
            <li>
              brak profilaktyki po zabiegu chirurgicznym wrastajcych paznokci -
              problem czsto powraca ze zwielokrotnion si
            </li>
            <li> nadwaga, cia</li>
          </ul>
        </span>
        <br></br>
        <h3>
          Terapia leczenia wrastania paznokcia jest dobierana w zalenoci od
          stanu wrastania tego paznokcia. Jeli tendencja do wrastania stale si
          utrzymuje, paznokcie s zdeformowane, rurkowate, wystpuje dugo
          utrzymujcy si stan zapalny to wtedy zakadamy klamry.
        </h3>
        <br></br>
        <p>
          Klamra umoliwia ona prostowanie pytki paznokciowej i zachowanie
          prawidowego toru jej wzrostu. Jej rodzaj dopasowywany jest
          indywidualnie do kadego pacjenta. Pod uwag brane s gwnie:
          ksztat, grubo, elastyczno paznokcia i sposb wrastania.
        </p>
        <br></br>
        <p>
          Zabieg zakadania klamry jest dla pacjenta bezbolesny. Prawidowo
          zaoona klamra na paznokcie jest niewyczuwalna. W pierwszych dniach
          pacjent moe odczuwa lekkie ciskanie lub cignicie - jednak nie ma
          powodw do obaw.
        </p>
        <br></br>
        <p>
          Klamry ortonyksyjne s dyskretne i w aden sposb nie zaburzaj
          codziennej aktywnoci. Jednak aby terapia bya skuteczna, naley
          stosowa si do zalece podologa. S to:
        </p>
        <br></br>
        <span className="numbers-li">
          <ol>
            <li>
              Zrezygnowanie z noszenia szpilek i butw z wskimi noskami na czas
              terapii.
            </li>
            <br></br>
            <li>
              Unikanie dugotrwaego moczenia stp. Rozmoczona pytka
              paznokciowa moe nieprawidowo reagowa na dziaanie klamry.
              Natomiast standardowe kpiele i czynnoci higieniczne s
              dozwolone.
            </li>
            <br></br>
            <li>
              Zgaszanie si na kontrole podologiczne. Kontrole klamry
              ortonyksyjnej zwykle odbywaj si co 3-4 tygodnie. S one bardzo
              wane i nie naley ich pomija. W ich trakcie podolog kontroluje
              tor wzrostu paznokcia oraz zmienia klamr na inn (dostosowujc
              jej si, kierunek pracy i sprysto do aktualnego stanu
              paznokcia).
            </li>
          </ol>
        </span>
      </main>
    </section>
  );
};
