import React, { useState } from "react";
import jsonPodologia from "../Json/podologia.json";

import SelectDetailesOfPodologia from "../podologia/procedureDetails/SelectDetailesOfPodologia";
import "./stylePodologia/podologia.scss";

import MySwiper from "../../swiper/MySwiper";

function Podologia() {
  const [idItems, setIdItems] = useState(-1);

  const clickChooseSelect = (data) => {
    setIdItems(data);
  };

  return (
    <div className="podologia">
      <header className="podologia--header">
        <p className="podologia__header--p">
          W swojej pracy cz zamiowanie do podologii z chci pomagania
          innym. Motywacj do pracy jest dla mnie nieustanne niesienie ulgi
          potrzebujcym pacjentom. W gabinecie utrzymuj medyczny standard
          higieny. Stosuj nowoczesne i skuteczne metody terapii.
        </p>
        <p className="podologia__header--p">
          W dbaoci o Pastwa bezpieczestwo przy kadym zabiegu wykorzystywane
          s tylko wysterylizowane narzdzia oraz zachowywane s wszelkie zasady
          higieny.
        </p>
      </header>
      <MySwiper json={jsonPodologia} clickChooseSelect={clickChooseSelect} />
      <SelectDetailesOfPodologia id={idItems} />
    </div>
  );
}

export default Podologia;
