import React from "react";

export const Henna = () => {
  return (
    <div>
      <h1 className="h1">Henna</h1>
      <p>
        Henna brwi i rzs to zabieg polegajcy na delikatnym farbowaniu woskw
        brwi oraz rzs. Efekt utrzymuje si na rzsach przez okoo 3-4 tygodnie,
        na brwiach okoo 2 tygodni. Kolor henny jest dobierany indywidualnie.
      </p>
      <br></br>
      <h3>Przeciwwskazania do zabiegu:</h3>

      <span className="li">
        <ul>
          <li> uczulenie na henn</li>
          <li>stany zapalne i alergiczne skry</li>
          <li> choroby i uszkodzenia oka (np. jczmie, zapalenie spojwek)</li>
          <li> uszkodzenia lub otarcia naskrka w okolicy brwi</li>
          <li>soczewki kontaktowe</li>
        </ul>
      </span>
      <h3>Regulacj wykonujemy pset oraz woskiem.</h3>
    </div>
  );
};
