import React from "react";

export const DiamentowyLift = () => {
  return (
    <div>
      <h1 className="h1"> Zabieg Diamentowy - Bankietowy </h1>
      <p>
        Diamentowy (Lift) to zabieg bankietowy silnie stymulujcy skr. Opiera
        si na niezmiernie bogatym w dobroczynne pierwiastki, diamentowym pyle,
        ktry ma oczyszczajce dziaanie na skr. Zabieg napina skr twarzy
        oraz nadaje jej nieskazitelny wrcz bysk - dziki ktremu cera ju na
        pierwszy rzut oka jest zauwaalnie odmodzona. Wskazania to tego zabiegu
        s nastpujce: skra pozbawiona elastycznoci, skra zwiotczaa oraz
        skra wymagajca stymulacji. Gwnym celem zabiegu jest natychmiastowa
        poprawa napicia skry oraz modelowanie owalu twarzy.
      </p>
    </div>
  );
};
