import React from "react";

export const MasazKamieniami = () => {
  return (
    <div>
      <h1 className="h1">Masa gorcymi kamieniami</h1>
      <p className="pp">
        Masa gorcymi kamieniami - wprowadza w stan gbokiego relaksu,a
        wszystko to za spraw rozgrzanych kamieni wulkanicznych przy pomocy
        ktrych wykonywany jest zabieg. Ciepo uwalniajce si podczas masau
        niweluje uczucie stresu poprzez redukcj napi miniowych.
      </p>
    </div>
  );
};
