import React from "react";

export const MasazNaBalsamie = () => {
  return (
    <div>
      <h1 className="h1">Masa na balsamie</h1>
      <p className="pp">
        Masa na balsamie - balsam arajas uyty podczas tego masau w swoim
        skadzie zawiera maso shea, maso oliwne oraz olej z awokado. Regularne
        korzystanie z masau na balsamie pozwoli utrzyma skr w dobrej
        kondycji oraz zabezpieczy j przez pojawianiem si rozstpw.
      </p>
    </div>
  );
};
