import React from "react";

export const MezoterapiaBeziglowa = () => {
  return (
    <div>
      <h1 className="h1">Mezoterapia bezigowa</h1>
      <p className="pp">
        Zabieg mezoterapii bezigowej stanowi doskona alternatyw dla
        klasycznej mezoterapii, gdy pozwala na wyeliminowanie skutkw ubocznych
        zwizanych z iniekcjami. Daje moliwo zastosowania na praktycznie
        kadej czci ciaa, ze wzgldu na szerokie spektrum swojego dziaania.
      </p>
      <br></br>
      <p>
        W mezoterapii bezigowej zastosowano poczenie terapii skojarzonej z
        elektroporacj i wprowadzaniem preparatu w gb skry. Takie poczenie
        gwarantuje bardzo szybko dugotrwae efekty.
      </p>
      <br></br>
      <h3>
        Dziaanie mezoterapii uzalenione jest od problemu skry, jej kondycji i
        okolicy poddawanej zabiegowi:
      </h3>
      <span>
        <ul>
          <li>dziaanie anti-aging</li>
          <li>dziaanie modelujce</li>
          <li>dziaanie napinajce</li>
          <li>dziaanie rozjaniajce</li>
          <li>dziaanie wzmacniajce naturaln odporno skry</li>
          <li>dziaanie uszczelniajce naczynia krwionone</li>
          <li>dziaanie regulujce serum</li>
        </ul>
      </span>
      <p>
        Na twarz, szyj i dekolt proponujemy wykonanie 4-6 zabiegw co dwa
        tygodnie.
      </p>
    </div>
  );
};
